<template>
    <el-main>
      <el-row>
        <el-col :span="24">
           <el-button style="float:left" type="primary" icon="el-icon-back" @click="goback()"></el-button>
          <el-button style="float:left"  type="primary" icon="el-icon-refresh" @click="init()"></el-button>
          <div class="demonstration h_title">ETCD<font> </font></div>
         
           <el-button  type="primary" icon="el-icon-plus" @click="handleData()"></el-button>
        </el-col>
      </el-row>

  <el-table :data="tableData"  :row-style="{height:'40px'}"   :cell-style="{padding:'0'}" style="font-size: 15px">

        <el-table-column label="key" >
          <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.key }}</span>
          </template>
        </el-table-column>
           <el-table-column label="value" >
          <template slot-scope="scope">
                <span style="margin-left: 10px">{{ scope.row.value }}</span>
          </template>
        </el-table-column>
  <el-table-column :label="getlan('operate')" width="360">
          <template slot-scope="scope">

             
             <el-button size="small"   icon="el-icon-edit" @click="handleData(scope.$index, scope.row)"></el-button>
             <el-button size="small" icon="el-icon-delete"  @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>

    
      </el-table>

<div class="block">
            <span class="demonstration"></span>
           <el-pagination 
                layout="prev,next" 
                @size-change="handleSizeChange"
                @current-change="current_change"
                :page-sizes="[50,200, 300]"
                :page-size="this.pagesize"
                :total="this.total">
            </el-pagination>
        </div>

     <el-dialog :title="this.form.mode" :visible.sync="dialogServer"  :width="this.getDialogWidth()"   :show-close="true" :close-on-click-modal="false" :close-on-press-escape="false">
        <el-form :model="form" :rules="rules" ref="form" class="el-form--label-top">

      <el-form-item label="" prop="key"> 
            <span  class="tlabel">key</span>
            <el-input style="width:500px" v-model="form.key" auto-complete="off" placeholder="key"></el-input> 
            </el-form-item>
        <el-form-item label="" prop="value"> 
          <span  class="tlabel">value</span>
            <el-input  style="width:500px"  v-model="form.value" auto-complete="off" placeholder="value"></el-input> 
       </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
                <el-button @click="handleClose('form')">{{getlan('cancel')}}</el-button>
                <el-button type="primary" @click="comfirmSubmit()">{{getlan('Execute')}}</el-button>
            </span>
      </el-dialog>
    </el-main>
</template>

<script>
import Qs from 'qs'
    import{Type,AddData,DeleteData,GetList,AddKV} from "@/api/index"
     import{getDay,getDateTime,getReqUrl,getLang,getValue,setValue} from "@/utils/pub"
    export default {
        name: 'App',
        data() {
        
            return {
      
           cols: [
           {prop: 'table_name', label: 'table_name'},
           {prop: 'table_catalog', label: 'table_catalog'},
          ],
              pageid: 1,
              total:50,
            pagesize: 50,
              loadFinish:0,
               tableData:[{table_name:"",table_catalog:""}],
              CategoryName:'',
             dialogServer:false,
              
              sqlString:'',
              form:{
                mode:"",
               key:"",
               value:"",
                
              },
               querymap:{ 
               pageid:this.pageid,
                pagesize:this.pagesize,
                table:'etcd',
                dbname:"",
              },
              rules:{
               key:[
                  { required: true, message: 'Please input name', trigger: 'change' },
                  { min: 1, max: 30, message: 'length 1 - 30', trigger: 'change' }
                ],
              }
            }
        },
        created(){
          this.belong=this.$route.params.belong;
          this.init();
        },
         methods: {
          getlan(name){ var str=this.$t('lang.'+name+''); if(str.indexOf("lang.")>=0){ return name; }return str },
          init(){
            //this.total=this.tableData.length;
            
        if(this.$route.params.dbname!=null){
             this.querymap.dbname=this.$route.params.dbname;
              setValue("dbname",this.$route.params.dbname)
          }else{
           this.querymap.dbname=getValue("dbname");
          }

              this.errorMessage="Loading..."
            GetList(Qs.stringify(this.querymap)).then(res=>{
            
              this.tableData = res.data.data;
               //this.total=res.data.total;
               this.pagesize=res.data.pagesize;
               this.errorMessage=""
            })

          },
      
           goback(){
	 this.$router.push({name:'database'});
          },
          addtable(){
         
          },
        
      
          handleData(index, row){
            if(!index && !row){
               this.form.mode='add'
              this.dialogServer = true;
            }else{
            this.form.key=row.key;
             this.form.mode='edit'
               this.form.value=row.value;
               this.dialogServer = true;
            }
              
          },

          handleDelete(index, row) {
              this.form.mode='delete'
               this.form.key=row.key;
               this.form.value=row.value;
              this.dialogServer = true;
          },
          handleClose(formName){
            this.dialogServer = false;
            this.form = {
              id:0,
              key:'',

            }
            this.$refs['form'].resetFields();
          },//分页
        current_change:function(pageid){
            this.pageid = pageid;
  GetList(Qs.stringify(this.querymap)).then(res=>{
            
              this.tableData = res.data.data;
              // this.total=res.data.total;
               this.pagesize=res.data.pagesize;
            })

        },
        handleSizeChange(val) {
            this.pagesize = val;
            
        },
         
    
          comfirmSubmit(){
         
    this.$confirm( this.sqlString,  this.getlan('Execute'), {
              confirmButtonText:  this.getlan('Execute'),
              confirmButtonClass: 'el-button--danger',
              cancelButtonText:  this.getlan('cancel'),
              type: 'warning'
            }).then(() => {
                this.Submit()
            }).catch(() => {});
          },
    Submit() {
          
           if(this.form.key==''){
             this.alert_ok('key is null')
             return
            }
            if(this.form.value==''){
             this.alert_ok('value is null')
             return
            }

            AddKV(Qs.stringify(this.form)).then(res=>{
             
              if(res.data.code==1){
                
                this.dialogServer = false;
                this.form.key=""
                this.form.value=""
                this.init();
                this.$message({message: 'success',type: 'success'});
              }else{
                   this.$message.error(res.data.message);
              }
            })

          }
        }
    }
</script>

<style lang="scss" scoped>
  .el-main {
    margin: 0px 0 0 160px;
    padding: 10px;
    .el-col {
      text-align: right;
    }
    .el-form--label-top .el-form-item__label {
      padding: 0;
    }
    .el-button.add {
      background-color: #4caf50;
      border: 0;
      font-size: 12px;
    }
     .h_title{
      margin-left:20px;
      float: left;
      line-height: 40px;
      font{
        color: #4caf50;
      }
    }
  }
</style>



